.portfolio {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 30px 0;
  border-top: 1px solid white;
  border-bottom: 1px solid white;
}

.portfolio-title {
  font-size: 24px;
  margin-bottom: 20px;
  padding-bottom: 5px;
}

@media screen and (min-width: 768px) {
  .portfolio-title {
    font-size: 36px;
  }
}

.song {
  background-color: white;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 10px;
  overflow: hidden;
  padding: 5px;
  position: relative;
  width: 90px;
}

.song-image {
  width: 100%;
  height: 100%;
}

.song-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  bottom: -90px;
  color: black;
  height: 90px;
  position: absolute;
  width: calc(100% - 10px);
  transition: all 0.3s ease-in-out;
}

.song-content span {
  display: block;
  text-align: center;
  font-size: 14px;
}

.song-content span:last-child {
  color: gray;
  font-size: 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.song:hover .song-content,
.song-image:hover .song-content {
  transform: translate(0, -90px);
}

@media screen and (min-width: 768px) {
  .song {
    width: 125px;
  }

  .song-content {
    bottom: -125px;
    height: 125px;
  }

  .song:hover .song-content,
  .song-image:hover .song-content {
    transform: translate(0, -125px);
  }
}

@media screen and (min-width: 992px) {
  .song {
    width: 170px;
  }

  .song-content {
    bottom: -80px;
    height: 80px;
  }

  .song:hover .song-content,
  .song-image:hover .song-content {
    transform: translate(0, -80px);
  }
}
