.principal-main,
.principal-portfolio,
.footer {
  padding-top: 50px;
  padding-bottom: 50px;
}

.principal-main,
.principal-portfolio {
  margin-bottom: 30px;
}

.principal-main {
  font-size: 16px;
  min-height: 50vh;

  h2 {
    font-size: 2em;
    font-weight: 700;
    text-align: left;
  }

  h6 {
    color: black;
    font-size: 1.13em;
    line-height: 31px;
    text-align: right;

    span {
      background: rgb(255, 255, 255);
      padding: 5px 3px;
    }
  }
}

@media screen and (min-width: 768px) {
  .principal-main {
    font-size: 22px;

    h6 {
      line-height: 48px;

      span {
        padding: 10px;
      }
    }
  }
}

.principal-portfolio {
  background: black;

  h3 {
    font-weight: 700;
  }
}

.footer {
  .col {
    text-align: center;
  }

  .footer-social {
    display: flex;

    div {
      text-align: center;
      font-size: 14px;

      .fa-facebook-f {
        padding: 10px 13px;
      }
      .fa-twitter {
        padding: 10px 10px;
      }
      .fa-instagram {
        padding: 10px 11px;
      }
    }
  }
}
