.marginTop80 {
  margin-top: 80px;
}

.about-title {
  font-size: 24px;
  margin-bottom: 20px;
  padding-bottom: 5px;
}

.about-article {
  font-size: 16px;
  text-align: justify;
}

.about__founder {
  &-image {
    display: inline-block;
    width: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 20px;

    img {
      width: inherit;
    }
  }
}

@media screen and (min-width: 768px) {
  .about-title {
    font-size: 36px;
  }

  .about-article {
    font-size: 20px;
  }

  .about__founder {
    &-image {
      display: inline-block;
      width: 300px;
      height: 300px;
      overflow: hidden;
      border-radius: 50%;
      margin-bottom: 20px;

      img {
        width: inherit;
      }
    }
  }
}
