.equipo {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  margin-bottom: 20px;
}

.equipo-icon,
.equipo-subtitle {
  align-self: center;
}

.equipo-icon {
  border-radius: 50%;
  font-size: 22px;
  height: 50px;
  padding: 9px 14px;
  width: 50px;

  &.mic {
    padding: 9px 18px;
  }
}

.equipo-title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  padding-bottom: 5px;
}

@media screen and (min-width: 768px) {
  .equipo-title {
    font-size: 36px;
  }
}

.equipo-subtitle {
  font-size: 1.125em;
}

.equipo-list {
  list-style-type: none;
  margin: 0;
  padding: 0px;

  li {
    font-size: 1em;
    margin-bottom: 5px;
  }

  li span {
    display: block;
  }
  li span:last-child {
    font-size: 0.875em;
  }
}

@media screen and (min-width: 768px) {
  .equipo {
    font-size: 18px;
  }
}

@media screen and (min-width: 992px) {
  .equipo {
    font-size: 22px;
  }
}
