.songActions {
  text-align: right;

  button {
    margin-left: 20px;
  }
}

.songForm {
  small {
    color: red;
  }
}

.songList {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.songItem {
  background: rgba(0, 0, 0, 0.4);
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 10px;
  width: 100%;

  img {
    background: white;
    height: 78px;
    width: 78px;
  }

  .songInfo {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 150px;

    span,
    a {
      display: block;
    }

    .info-artist,
    .info-album,
    a {
      font-size: 14px;
    }

    a {
      color: white;
    }
  }

  .songAction {
    width: 44px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

@media screen and (min-width: 768px) {
  .songItem {
    width: 320px;
  }
}

.feather {
  width: 18px;
  height: 18px;
  stroke: currentColor;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  fill: none;
}
